.root{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f9f9f9;
   justify-content: flex-start;
   height: max-content;
   text-align: right;
   position: relative;
}

.inputBox{
    display: flex;
    flex-direction: column;
    width: 30%;
    margin: auto;
    border:1px solid  #f9f9f9;;
    background-color: white;
    -webkit-box-sizing: border-box;
    padding:10px;
}
.input{
    width: 90%;
    margin:auto;
    height: 44px;
    border-radius: 5px;
}
.btn{
    width: 60%;
    height:50px;
    margin:auto;
    margin-top: 20px;
    background-color: #0084ff;
    border-radius: 5px;
    border:1px solid #0084ff;
    color:white;
    font-size: large;
    
}
.fgbtn{
    background-color: inherit;
    border: none;
}